import { Pegawai_pendidikan } from 'src/app/sdk/hr/models/pegawai-_pendidikan';
import { NzMessageService } from 'ng-zorro-antd';
import { Pegawai_keluarga } from 'src/app/sdk/hr/models/pegawai-_keluarga'
import { V_pegawai_posisi } from 'src/app/sdk/hr/models/v_pegawai-_posisi'
import { Pegawai_biodata } from 'src/app/sdk/hr/models/pegawai-_biodata'
import { Pegawai } from 'src/app/sdk/hr/models/pegawai'
import { Router, ActivatedRoute } from '@angular/router'
import { NgxSpinnerService } from 'ngx-spinner'
import { DirektoriPegawaiService } from 'src/app/sdk/hr/services/direktori-pegawai.service'
import { Component, OnInit } from '@angular/core'
import { DirektoriPegawaiBiodataService, DirektoriPegawaiPendidikanService, DirektoriPegawaiPengalamanService, } from 'src/app/sdk/hr/services'
import { PegawaiDetailService } from '../../service/pegawai-detail.service'
import { DirektoriPegawaiKeluargaService } from 'src/app/sdk/hr/services/direktori-pegawai-keluarga.service'
import { Pegawai_pengalaman } from 'src/app/sdk/hr/models';

@Component({
    selector: 'app-detail',
    templateUrl: './detail.component.html',
    styleUrls: ['./detail.component.scss'],
})
export class DetailComponent implements OnInit {

    constructor(
        private direktoriPegawaiService: DirektoriPegawaiService,
        private direktoriPegawaiBiodataService: DirektoriPegawaiBiodataService,
        private direktoriPegawaiKeluargaService: DirektoriPegawaiKeluargaService,
        private direktoriPegawaiPendidikanService: DirektoriPegawaiPendidikanService,
        private direktoriPegawaiPengalamanService: DirektoriPegawaiPengalamanService,
        private spinner: NgxSpinnerService,
        private router: Router,
        private pegawaiDetailService: PegawaiDetailService,
        private nzMessageService: NzMessageService
    ) { }

    pegawai: Pegawai = {}
    biodata: Pegawai_biodata = {}
    pegawai_posisi: V_pegawai_posisi = {}
    dataKeluarga: Pegawai_keluarga[] = []
    dataPendidikan: Pegawai_pendidikan[] = []
    dataPengalaman: Pegawai_pengalaman[] = []

    id_pegawai_biodata: string = ''
    id_pegawai: string = ''

    ngOnInit() {
        this.pegawaiDetailService.getIdPegawai().subscribe(data => {
            this.id_pegawai = data
            console.log('id pegawai paja tu ' + this.id_pegawai)
        })
        this.getPegawai()
        this.getKeluarga()
        this.getPendidikan()
        this.getPengalaman()
    }

    getPegawai() {
        this.direktoriPegawaiService.getDirektoriPegawaisId(this.id_pegawai).subscribe(data => {
            this.pegawai = data.data
            this.getBiodata(data.data.id_pegawai)
        })
    }

    getBiodata(id_pegawai: string) {
        this.direktoriPegawaiBiodataService
            .getDirektoriPegawaiBiodatasId(id_pegawai)
            .subscribe(res => {
                this.biodata = res.data
            })
    }

    //------------------------------------------------Keluarga------------------------------------------------------//
    getKeluarga() {
        this.spinner.show()
        this.direktoriPegawaiKeluargaService
            .getDirektoriPegawaiKeluargas(JSON.stringify({ id_pegawai: this.id_pegawai }))
            .subscribe(res => {
                this.dataKeluarga = res.data
                this.spinner.hide()
            })
    }

    delete(id) {
        this.spinner.show()
        this.direktoriPegawaiKeluargaService
            .deleteDirektoriPegawaiKeluargasId(id)
            .subscribe(data => {
                this.getKeluarga()
                this.spinner.hide()
            })
    }

    batalDelete() {
        this.nzMessageService.info('click cancel')
    }

    //------------------------------------------------Pendidikan------------------------------------------------------//
    getPendidikan() {
        this.spinner.show()
        this.direktoriPegawaiPendidikanService.getDirektoriPegawaiPendidikans(JSON.stringify({ id_pegawai: this.id_pegawai })).subscribe(
            data => {
                this.dataPendidikan = data.data
                this.spinner.hide()
            }
        )
    }

    deletePendidikan(id) {
        this.spinner.show()
        this.direktoriPegawaiPendidikanService
            .deleteDirektoriPegawaiPendidikansId(id)
            .subscribe(data => {
                this.getPendidikan()
                this.spinner.hide()
            })

    }

    batalDeletePendidikan() {
        this.nzMessageService.info('click cancel')
    }

    //------------------------------------------------Pengalaman------------------------------------------------------//
    getPengalaman() {
        this.spinner.show()
        this.direktoriPegawaiPengalamanService.getDirektoriPegawaiPengalamen(JSON.stringify({ id_pegawai: this.id_pegawai })).subscribe(
            data => {
                this.dataPengalaman = data.data
                this.spinner.hide()
            }
        )
    }

    deletePengalaman(id) {
        this.spinner.show()
        this.direktoriPegawaiPengalamanService
            .deleteDirektoriPegawaiPengalamenId(id)
            .subscribe(data => {
                this.getPengalaman()
                this.spinner.hide()
            })

    }

    batalDeletePengalaman() {
        this.nzMessageService.info('click cancel')
    }
}
