import { NgModule } from '@angular/core'
import { CommonModule } from '@angular/common'

import { BiodataRoutingModule } from './biodata-routing.module'
import { DetailComponent } from './detail/detail.component'
import { NgZorroAntdModule } from 'ng-zorro-antd'
import { ReactiveFormsModule, FormsModule } from '@angular/forms'
import { PerfectScrollbarModule } from 'ngx-perfect-scrollbar'
import { EditBiodataComponent } from './edit-biodata/edit-biodata.component'
import { FormBuilderTypeSafe } from 'src/app/services/angular-reactive-forms-helper'


@NgModule({
    declarations: [
        DetailComponent,
        EditBiodataComponent,
    ],
    imports: [
        CommonModule,
        BiodataRoutingModule,
        NgZorroAntdModule,
        ReactiveFormsModule,
        FormsModule,
        PerfectScrollbarModule,
    ],
    providers:
        [
            FormBuilderTypeSafe
        ],
})
export class BiodataModule { }
