import { NgModule } from '@angular/core'
import { Routes, RouterModule } from '@angular/router'
import { DetailComponent } from './detail/detail.component'
import { EditBiodataComponent } from './edit-biodata/edit-biodata.component'
import { DokumenComponent } from './dokumen/dokumen.component'
import { FotoComponent } from './foto/foto.component'


const routes: Routes = [
    {
        path: 'dokumen',
        component: DokumenComponent,
    },
      {
        path: 'detail',
        component: DetailComponent,
    },
    {
        path: 'foto',
        component: FotoComponent
    },
    {
        path: 'edit-biodata',
        component: EditBiodataComponent,
    },
    {
        path: 'pendidikan',
        loadChildren: () => import('./pendidikan/pendidikan.module').then(mo => mo.PendidikanModule),
    },
    {
        path: 'keluarga',
        loadChildren: () => import('./keluarga/keluarga.module').then(mo => mo.KeluargaModule),
    },
    {
        path: 'pengalaman',
        loadChildren: () => import('./pengalaman/pengalaman.module').then(mo => mo.PengalamanModule),
    },
    {
        path: 'sertifikasi',
        loadChildren: () => import('./sertifikasi/sertifikasi.module').then(mo => mo.SertifikasiModule),
    },
       {
        path: 'id-card',
        loadChildren: () => import('./id-card/id-card.module').then(mo => mo.IdCardModule),
    },
    {
        path: 'keahlian',
        loadChildren: () => import('./keahlian/keahlian.module').then(mo => mo.KeahlianModule),
    },
]

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule],
})
export class BiodataRoutingModule {}
