import { Component, OnInit } from '@angular/core';
import { DirektoriPegawaiService } from 'src/app/sdk/hr/services';
import { Pegawai } from 'src/app/sdk/hr/models';
import { SingleUploadService } from 'src/app/components/document/single-upload.service';
import { NgxSpinnerService } from 'ngx-spinner';
import { FormBuilderTypeSafe, FormGroupTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { ActivatedRoute, Router } from '@angular/router';
import { FormControl, Validators } from '@angular/forms';
import { PegawaiDetailService } from '../../service/pegawai-detail.service';

@Component({
    selector: 'app-foto',
    templateUrl: './foto.component.html',
    styleUrls: ['./foto.component.scss']
})
export class FotoComponent implements OnInit {

    constructor(
        private direktoriPegawaiService: DirektoriPegawaiService,
        private pegawaiDetailService: PegawaiDetailService,
        private singleUploadService: SingleUploadService,
        private spinner: NgxSpinnerService,
        private fb: FormBuilderTypeSafe,
        private pesan: PesanService,
        private activatedRoute: ActivatedRoute,
        private router: Router
    ) { }

    id_pegawai: string = ''
    toogleUpload = false

    dataPegawai: Pegawai = {}
    ngOnInit() {
        this.pegawaiDetailService.getIdPegawai().subscribe(data => {
            this.id_pegawai = data
            console.log('id pegawai paja tu ' + this.id_pegawai)
        })
        this.getData()
        this.form()

        this.singleUploadService.getCallback().subscribe(
            data => {

                if (data == 'company.finish') {
                    this.toogleUpload = false;
                }
            }
        )

        this.singleUploadService.getId().subscribe(
            data => {
                if (data) {
                    this.dataForm.patchValue({ foto: data })
                    this.simpan()
                }
            }
        )
    }

    // togleShowUpload() {

    //     this.toogleUpload = true;
    // }
    foto = 'https://os.alipayobjects.com/rmsportal/QBnOOoLaAfKPirc.png'
    getData() {
        this.direktoriPegawaiService.getDirektoriPegawaisId(this.id_pegawai).subscribe(
            data => {
                this.dataPegawai = data.data;

                this.dataForm.setValue(data.data)

                this.singleUploadService.param.next({
                    for_module: "hr_pegawai",
                    for_table: "erp_hr.pegawai",
                    for_module_id: data.data.id_pegawai
                })

                // if (data.data.foto) {
                //     this.foto = "http://erp-document.vm.devetek.com/download?id_document=" + data.data.foto
                // }

            }
        )
    }

    dataForm: FormGroupTypeSafe<Pegawai>;
    form() {
        this.dataForm = this.fb.group<Pegawai>(
            {
                no_induk: new FormControl('', [Validators.required]),
                no_absensi: new FormControl(''),
                nama_belakang: new FormControl(''),
                nama_depan: new FormControl(''),
                jenis_kelamin: new FormControl(''),
                kewarganegaraan: new FormControl(''),
                type_pegawai: new FormControl(''),
                tanggal_masuk: new FormControl(''),
                hp: new FormControl(''),
                id_ptkp: new FormControl(''),
                id_klaim_kategori: new FormControl(''),
                id_jadwal_tipe: new FormControl(''),
                email: new FormControl(''),
                status_pegawai: new FormControl(''),
                id_pegawai: new FormControl(''),
                id_user_create: new FormControl(''),
                bpjs_kesehatan: new FormControl(''),
                bpjs_jp: new FormControl(''),
                bpjs_jkm: new FormControl(''),
                bpjs_jkk: new FormControl(''),
                bpjs_jht: new FormControl(''),
                agama: new FormControl(''),
                tanggal_lahir: new FormControl(''),
                tempat_lahir: new FormControl(''),
                hp2: new FormControl(''),
                status_perkawinan: new FormControl(''),
                phone: new FormControl(''),
                created_at: new FormControl(''),
                updated_at: new FormControl(''),
                tanggal_keluar: new FormControl(''),
                foto: new FormControl('')

            }
        )
    }

    simpan() {
        this.spinner.show()
        if (this.dataForm.invalid) {
            this.pesan.pesanWarningForm('Form Tidak Valid');
            this.spinner.hide()
            return;
        }

        this.dataPegawai = this.dataForm.value;
        this.direktoriPegawaiService.putDirektoriPegawaisId({ id: this.id_pegawai, body: this.dataPegawai }).subscribe(
            data => {
                this.toogleUpload = false
                this.getData()
                this.goToBack()
                this.spinner.hide()
            }, err => {
                this.pesan.pesanWarningForm('Gagal di simpan');
                this.spinner.hide()
            },
            () => {
                console.log('HTTP request completed.')
            }
        )

    }


    goToBack() {
        this.router.navigate(['/hr/direktori/pegawai-detail', this.id_pegawai])
    }

}
