import { Component, OnInit } from '@angular/core';
import { FileDocumentService } from 'src/app/sdk/document/services';
import { Document } from 'src/app/sdk/document/models';
import { Router } from '@angular/router';

import { NzMessageService } from 'ng-zorro-antd';
import { NgxSpinnerService } from 'ngx-spinner';
import { SingleUploadService } from 'src/app/components/document/single-upload.service';
import { DirektoriPegawaiService } from 'src/app/sdk/hr/services';
import { Pelamar, Pegawai } from 'src/app/sdk/hr/models';
import { PegawaiDetailService } from '../../service/pegawai-detail.service';

@Component({
    selector: 'app-dokumen',
    templateUrl: './dokumen.component.html',
    styleUrls: ['./dokumen.component.scss']
})
export class DokumenComponent implements OnInit {

    constructor(
        private fileDocumentService: FileDocumentService,
        private router: Router,
        private pegawaiDetailService: PegawaiDetailService,
        private direktoriPegawaiService: DirektoriPegawaiService,
        private nzMessageService: NzMessageService,
        private spinner: NgxSpinnerService,
        private singleUploadService: SingleUploadService
    ) { }

    toogleUpload = false

    dataDokumen: Document[] = []
    dataPegawai: Pegawai = {}

    id_pegawai: string = ''

    ngOnInit() {
        this.pegawaiDetailService.getIdPegawai().subscribe(data => {
            this.id_pegawai = data


            this.getDokumen()
            this.getPegawai()
        })

        this.singleUploadService.getCallback().subscribe(
            data => {

                if (data == 'pegawai_document.finish') {
                    this.toogleUpload = false;
                }
            }
        )

        this.singleUploadService.getId().subscribe(
            data => {
                if (data) {
                    this.getDokumen()
                }
            }
        )
    }

    togleShowUpload() {

        this.toogleUpload = true;
    }

    getPegawai() {
        this.direktoriPegawaiService.getDirektoriPegawaisId(this.id_pegawai).subscribe(
            data=> {
                this.dataPegawai = data.data;

                this.singleUploadService.param.next({
                    for_module: "pegawai_document",
                    for_table: "erp_hr_pegawai.pegawai",
                    for_module_id: data.data.id_pegawai
                })
            }
        )
    }
    getDokumen() {
        this.fileDocumentService.getFileDocuments(JSON.stringify({ id_pegawai: this.id_pegawai })).subscribe(
            data => {
                this.dataDokumen = data.data
            }
        )
    }

    delete(id) {
        this.spinner.show()
        this.fileDocumentService
            .deleteFileDocumentsId(id)
            .subscribe(data => {
                this.getDokumen()
                this.spinner.hide()
            })
    }

    batalDelete() {
        this.nzMessageService.info('click cancel')
    }

}
