import { FormControl, Validators } from '@angular/forms'
import {
    FormBuilderTypeSafe,
    FormGroupTypeSafe,
} from 'src/app/services/angular-reactive-forms-helper'
import { Pegawai_biodata } from 'src/app/sdk/hr/models/pegawai-_biodata'
import { Component, OnInit } from '@angular/core'
import { DirektoriPegawaiBiodataService } from 'src/app/sdk/hr/services'
import { NgxSpinnerService } from 'ngx-spinner'
import { Router } from '@angular/router'
import { PegawaiDetailService } from '../../service/pegawai-detail.service'
import { PesanService } from 'src/app/services/tool/pesan.service'

@Component({
    selector: 'app-edit-biodata',
    templateUrl: './edit-biodata.component.html',
    styleUrls: ['./edit-biodata.component.scss'],
})
export class EditBiodataComponent implements OnInit {
    constructor(
        private direktoriPegawaiBiodataService: DirektoriPegawaiBiodataService,
        private spinner: NgxSpinnerService,
        private router: Router,
        private pesan: PesanService,
        private pegawaiDetailService: PegawaiDetailService,
        private fb: FormBuilderTypeSafe,
    ) { }

    id_pegawai: string = ''
    pegawai_biodata: Pegawai_biodata = {}
    ngOnInit() {
        this.pegawaiDetailService.getIdPegawai().subscribe(data => {
            this.id_pegawai = data
            console.log('id pegawai paja tu ' + this.id_pegawai)
        })
        this.form()
        this.getBiodata()
    }

    getBiodata() {
        this.direktoriPegawaiBiodataService
            .getDirektoriPegawaiBiodatasId(this.id_pegawai)
            .subscribe(data => {
                this.dataForm.setValue(data.data)
            })
    }

    dataForm: FormGroupTypeSafe<Pegawai_biodata>
    form() {
        this.dataForm = this.fb.group<Pegawai_biodata>({
            id_pegawai: new FormControl('', [Validators.required]),
            id_pegawai_biodata: new FormControl(''),
            jenis_kelamin: new FormControl(''),
            tempat_lahir: new FormControl(''),
            tanggal_lahir: new FormControl(''),
            agama: new FormControl(''),
            status_perkawinan: new FormControl(''),
            golongan_darah: new FormControl(''),
            no_telepon: new FormControl(''),
            alamat_tinggal: new FormControl(''),
            kota_tinggal: new FormControl(''),
            provinsi_tinggal: new FormControl(''),
            no_identitas: new FormControl(''),
            tanggal_berakhir_identitas: new FormControl(''),
            alamat_identitas: new FormControl(''),
            kode_pos_tinggal: new FormControl(''),
            kota_identitas: new FormControl(''),
            prvinsi_identitas: new FormControl(''),
            kode_pos_identitas: new FormControl(''),
            hp1: new FormControl(''),
            email: new FormControl(''),
            hp2: new FormControl(''),
            bentuk_tubuh: new FormControl(''),
            bentuk_wajah: new FormControl(''),
            warna_kulit: new FormControl(''),
            warna_rambut: new FormControl(''),
            initial: new FormControl(''),
            nationality: new FormControl(''),
            tinggi_badan: new FormControl(''),
            berat_badan: new FormControl(''),
            id_user: new FormControl(''),
            created_at: new FormControl(''),
            updated_at: new FormControl(''),
        })
    }

    simpan() {
        this.spinner.show()
        if (this.dataForm.invalid) {
            this.pesan.pesanWarningForm('Form tidak valid')
            this.spinner.hide()
            return
        }
        this.pegawai_biodata = this.dataForm.value
        this.direktoriPegawaiBiodataService
            .putDirektoriPegawaiBiodatasId({ id: this.id_pegawai, body: this.pegawai_biodata })
            .subscribe(
                data => {
                    this.goToBack()
                    this.spinner.hide()
                },
                err => {
                    this.pesan.pesanWarningForm('Gagal di simpan')
                    this.spinner.hide()
                },
                () => {
                    console.log('HTTP request completed.')
                },
            )
    }

    goToBack() {
        this.router.navigate(['/profile/biodata/detail'])
    }
}
